.Home {
  display: flex;
  justify-content: space-around;

  padding: 20px 60px 20px 60px;
}

/* 레이아웃 */
.Home__left {
  width: 20%;
  padding: 10px;
}

.Home__left > div {
  padding: 20px;
  background-color: greenyellow;
}

.Home__left > div > div {
  margin-bottom: 20px;
}

.Home__right {
  width: 80%;
  padding: 10px;
}

.Home__right > div {
  padding: 20px;

  border: solid 1px grey;
  border-radius: 5px;
  
  background-color: rgb(234, 255, 47);
}

.Home__right > div > div {
  margin-bottom: 20px;
}

/* 컴포넌트 */
.Home__profile-img {
  width: 100%;
  height: 0;
  padding-bottom: 100%;

  border: solid 2px black;
  border-radius: 100%;
}